"use client";

import { useState, useEffect } from "react";
import { useSearchParams, useRouter } from "next/navigation";
import { supabase } from "../../../lib/supabaseClient";
import TransactionTable from "../transaction/components/transactionTable";
import TransactionModal from "../transaction/components/transactionModal";
import { useCashflowData } from "../dashboard/components/useCashflowData";

export default function TransactionsPage() {
  const router = useRouter();
  const searchParams = useSearchParams();

  // =========================
  // YEAR (SYNCED WITH DASHBOARD)
  // =========================
  const START_YEAR = 2025;
  const currentYear = new Date().getFullYear();

  const yearFromUrl = Number(searchParams.get("year"));
  const selectedYear =
    yearFromUrl && yearFromUrl >= START_YEAR
      ? yearFromUrl
      : currentYear >= START_YEAR
      ? currentYear
      : START_YEAR;

  // =========================
  // DATA FETCH (YEAR-AWARE)
  // =========================
  const {
    transactions: fetchedTransactions,
    loading,
    refetch,
  } = useCashflowData(selectedYear);

  const [transactions, setTransactions] = useState([]);
  const [showAddModal, setShowAddModal] = useState(false);

  useEffect(() => {
    setTransactions(fetchedTransactions || []);
  }, [fetchedTransactions]);

  // =========================
  // ADD TRANSACTION
  // =========================
 const handleAddTransaction = async (newTransaction) => {
  try {
    // ‚úÖ FORCE date to selected year if missing
    const transactionDate = newTransaction.date
      ? newTransaction.date
      : `${selectedYear}-01-01`;

    const lastBalance = transactions.length
      ? transactions[transactions.length - 1].balance
      : 0;

    const newBalance =
      lastBalance +
      (newTransaction.deposit || 0) -
      (newTransaction.withdrawal || 0);

    const transactionToInsert = {
      ...newTransaction,
      date: transactionDate, // ‚úÖ ENSURE YEAR MATCH
      balance: newBalance,
    };

    const { error } = await supabase
      .from("transactions")
      .insert([transactionToInsert]);

    if (error) throw error;

    // ‚úÖ DO NOT manually push to state
    refetch();
  } catch (err) {
    console.error("Add transaction failed:", err.message);
    alert("Error adding transaction.");
  }
};

  // =========================
  // DELETE TRANSACTION
  // =========================
  const handleDeleteTransaction = async (id) => {
    if (!id) return;
    if (!window.confirm("Delete this transaction?")) return;

    try {
      await supabase.from("transactions").delete().eq("id", id);

      const updated = transactions.filter((t) => t.id !== id);

      let runningBalance = 0;
      const recalculated = updated.map((t) => {
        runningBalance += (t.deposit || 0) - (t.withdrawal || 0);
        return { ...t, balance: runningBalance };
      });

      setTransactions(recalculated);
      refetch();
    } catch (err) {
      console.error("Delete failed:", err.message);
      alert("Error deleting transaction.");
    }
  };

  // =========================
  // EDIT TRANSACTION
  // =========================
  const handleEditTransaction = async (updatedTransaction, id) => {
    try {
      const index = transactions.findIndex((t) => t.id === id);
      if (index === -1) throw new Error("Transaction not found");

      const prevBalance = index > 0 ? transactions[index - 1].balance : 0;

      const newBalance =
        prevBalance +
        (updatedTransaction.deposit || 0) -
        (updatedTransaction.withdrawal || 0);

      const transactionToUpdate = {
        ...updatedTransaction,
        balance: newBalance,
      };

      const { error } = await supabase
        .from("transactions")
        .update(transactionToUpdate)
        .eq("id", id);

      if (error) throw error;

      const updated = [...transactions];
      updated[index] = transactionToUpdate;

      // Recalculate following balances
      let runBal = newBalance;
      for (let i = index + 1; i < updated.length; i++) {
        const t = updated[i];
        runBal += (t.deposit || 0) - (t.withdrawal || 0);
        updated[i] = { ...t, balance: runBal };

        await supabase
          .from("transactions")
          .update({ balance: runBal })
          .eq("id", t.id);
      }

      setTransactions(updated);
      refetch();
    } catch (err) {
      console.error("Edit failed:", err.message);
      alert("Error updating transaction.");
    }
  };

  
  const handleExportExcel = async () => {
    try {
      setLoading(true);
      setMessage("");
  
      const { data, error } = await supabase.from("transactions").select("*");
      if (error) throw error;
  
      if (!data || data.length === 0) {
        setMessage("‚ö†Ô∏è No data available to export.");
        return;
      }
  
      // Map your table fields correctly
      const formattedData = data.map((t) => ({
        Date: t.date || "",
        Description: t.description || "",
        Deposit: t.deposit != null ? t.deposit : 0,
        Withdrawal: t.withdrawal != null ? t.withdrawal : 0,
        Balance: t.balance != null ? t.balance : 0,
      }));
  
      const worksheet = XLSX.utils.json_to_sheet(formattedData);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Transactions");
  
      const today = new Date().toISOString().split("T")[0];
      XLSX.writeFile(workbook, `transactions_${today}.xlsx`);
  
      setMessage("‚úÖ Transactions exported to Excel successfully!");
    } catch (err) {
      console.error(err);
      setMessage("‚ùå Failed to export data: " + err.message);
    } finally {
      setLoading(false);
    }
  };

  // =========================
  // UI
  // =========================
 // =========================
return (
  <div className="w-full">

    {/* HEADER (HIDDEN ON PRINT) */}
    <div
      className="
        print:hidden
        flex flex-col md:flex-row md:items-center md:justify-between
        rounded-2xl
        bg-gradient-to-r from-emerald-900 via-emerald-800 to-emerald-700
        px-6 py-5
        shadow-sm
      "
    >
      <h1 className="text-2xl md:text-3xl font-semibold text-white">
        Transactions ({selectedYear})
      </h1>

      {/* ACTION BUTTONS */}
      <div className="flex flex-wrap gap-3 mt-4 md:mt-0">
        <button
          onClick={() => setShowAddModal(true)}
          className="px-4 py-2 rounded-lg bg-emerald-600 text-white font-medium hover:bg-emerald-700 shadow"
        >
          ‚ûï Add Transaction
        </button>

        <button
          onClick={refetch}
          className="px-4 py-2 rounded-lg bg-blue-600 text-white font-medium hover:bg-blue-700 shadow"
        >
          üì§ Export Transactions
        </button>

        <button
          onClick={() => window.print()}
          className="px-4 py-2 rounded-lg bg-slate-700 text-white font-medium hover:bg-slate-800 shadow"
        >
          üñ®Ô∏è Print
        </button>
      </div>
    </div>

    {/* TABLE (PRINT AREA) */}
    <div className="print-area bg-white rounded-xl shadow p-6 border border-slate-200 mt-5">
      <TransactionTable
        transactions={transactions}
        loading={loading}
        setTransactions={setTransactions}
        onAddTransaction={handleAddTransaction}
        onDeleteTransaction={handleDeleteTransaction}
        onEditTransaction={handleEditTransaction}
      />
    </div>

    {/* MODAL */}
    {showAddModal && (
      <TransactionModal
        onClose={() => setShowAddModal(false)}
        onSubmit={handleAddTransaction}
      />
    )}

  </div>
);
}