"use client";

import { useState } from "react";
import { Edit3, Trash2, Check, X, Search } from "lucide-react";

// Peso formatter
const currency = (val = 0) =>
  new Intl.NumberFormat("en-PH", {
    style: "currency",
    currency: "PHP",
    minimumFractionDigits: 2,
  }).format(val);

export default function TransactionTable({
  transactions = [],
  loading,
  onEditTransaction,
  onDeleteTransaction,
}) {
  const [editingId, setEditingId] = useState(null);
  const [rowForm, setRowForm] = useState({});

  /* ================= FILTER STATE ================= */
  const [filters, setFilters] = useState({
    type: "all",
    search: "",
    fromDate: "",
    toDate: "",
  });

  /* ================= FILTER LOGIC ================= */
  const filteredTransactions = transactions.filter((t) => {
    if (filters.type === "income" && t.deposit <= 0) return false;
    if (filters.type === "expense" && t.withdrawal <= 0) return false;

    if (filters.fromDate && new Date(t.date) < new Date(filters.fromDate))
      return false;
    if (filters.toDate && new Date(t.date) > new Date(filters.toDate))
      return false;

    if (
      filters.search &&
      !`${t.description ?? ""}`.toLowerCase().includes(filters.search.toLowerCase())
    )
      return false;

    return true;
  });

  /* ================= EDIT LOGIC ================= */
  const startEdit = (t) => {
    setEditingId(t.id);
    setRowForm({
      date: t.date,
      description: t.description,
      deposit: t.deposit || "",
      withdrawal: t.withdrawal || "",
    });
  };

  const cancelEdit = () => {
    setEditingId(null);
    setRowForm({});
  };

  const saveEdit = (id) => {
    onEditTransaction(
      {
        date: rowForm.date,
        description: rowForm.description,
        deposit: Number(rowForm.deposit || 0),
        withdrawal: Number(rowForm.withdrawal || 0),
      },
      id
    );
    cancelEdit();
  };

  

  return (
<div className="overflow-x-auto max-w-full">
    

      {/* TABLE */}
<div className="print-area">
  <div className="table-scroll-x overflow-x-auto max-w-full">
    <div className="table-scroll-y max-h-[800px] overflow-y-auto">
      <table className="transactions-table w-full min-w-[900px] text-sm">

            <thead className="bg-slate-50 border-b border-slate-200 sticky top-0 z-10">
              <tr className="text-slate-600">
                <th className="px-4 py-2 text-left">Date</th>
                <th className="px-4 py-2 text-left">Description</th>
                <th className="px-4 py-2 text-right">Deposit</th>
                <th className="px-4 py-2 text-right">Withdrawal</th>
                <th className="px-4 py-2 text-right">Balance</th>
                <th className="col-actions">Actions</th>

              </tr>
            </thead>

            <tbody className="divide-y divide-slate-100">
              {filteredTransactions.map((t) => {
                const isEditing = editingId === t.id;

                return (
                  <tr key={t.id} className="hover:bg-slate-50">

                    {/* DATE */}
                    <td className="px-4 py-2">
                      {isEditing ? (
                        <input
                          type="date"
                          value={rowForm.date}
                          onChange={(e) =>
                            setRowForm({ ...rowForm, date: e.target.value })
                          }
                          className="border rounded px-2 py-1 text-sm"
                        />
                      ) : (
                        t.date
                      )}
                    </td>

                    {/* DESCRIPTION */}
                    <td className="px-4 py-2">
                      {isEditing ? (
                        <input
                          type="text"
                          value={rowForm.description}
                          onChange={(e) =>
                            setRowForm({ ...rowForm, description: e.target.value })
                          }
                          className="border rounded px-2 py-1 text-sm w-full"
                        />
                      ) : (
                        t.description
                      )}
                    </td>

                    {/* DEPOSIT */}
                    <td className="px-4 py-2 text-right">
                      {isEditing ? (
                        <input
                          type="number"
                          value={rowForm.deposit}
                          onChange={(e) =>
                            setRowForm({ ...rowForm, deposit: e.target.value })
                          }
                          className="border rounded px-2 py-1 text-sm w-24 text-right"
                        />
                      ) : t.deposit ? (
                        <span className="text-emerald-700">
                          {currency(t.deposit)}
                        </span>
                      ) : (
                        "‚Äî"
                      )}
                    </td>

                    {/* WITHDRAWAL */}
                    <td className="px-4 py-2 text-right">
                      {isEditing ? (
                        <input
                          type="number"
                          value={rowForm.withdrawal}
                          onChange={(e) =>
                            setRowForm({ ...rowForm, withdrawal: e.target.value })
                          }
                          className="border rounded px-2 py-1 text-sm w-24 text-right"
                        />
                      ) : t.withdrawal ? (
                        <span className="text-rose-700">
                          {currency(t.withdrawal)}
                        </span>
                      ) : (
                        "‚Äî"
                      )}
                    </td>

                    {/* BALANCE */}
                    <td className="px-4 py-2 text-right font-semibold">
                      {currency(t.balance)}
                    </td>

                    {/* ACTIONS */}
{/* ACTIONS */}
<td className="col-actions px-4 py-2 text-center">
                      {isEditing ? (
                        <div className="flex justify-center gap-2">
                          <button
                            onClick={() => saveEdit(t.id)}
                            className="text-emerald-600 hover:text-emerald-800"
                          >
                            <Check size={16} />
                          </button>
                          <button
                            onClick={cancelEdit}
                            className="text-slate-400 hover:text-slate-600"
                          >
                            <X size={16} />
                          </button>
                        </div>
                      ) : (
                        <div className="flex justify-center gap-2">
                          <button
                            onClick={() => startEdit(t)}
                            className="text-slate-400 hover:text-slate-700"
                          >
                            <Edit3 size={15} />
                          </button>
                          <button
                            onClick={() => onDeleteTransaction(t.id)}
                            className="text-slate-400 hover:text-rose-600"
                          >
                            <Trash2 size={15} />
                          </button>
                        </div>
                      )}
                    </td>
                  </tr>
                );
              })}

              {!filteredTransactions.length && (
                <tr>
                  <td colSpan="6" className="px-6 py-10 text-center text-slate-400">
                    No transactions found
                  </td>
                </tr>
              )}
            </tbody>
          </table>

          {loading && (
            <div className="p-4 text-center text-slate-500">
              Loading transactions‚Ä¶
            </div>
          )}
        </div>
      </div>
      </div>
            </div>

  );
}
